#About{
    background-color:  rgb(8, 27, 41,0.2);
    backdrop-filter: blur(8px);
    padding: 56px;
    height: auto;
}
#About h1{
    text-align: center;
    margin-top: 20px;
}
.Box{
    margin: 30px;
    padding: 30px;
    background-color: rgb(0,0,0,0.5);
    backdrop-filter: blur(15px);
    display: flex;
    justify-content: center;
    align-items: center;
    border: #081b29;
    box-shadow: 0px 0px 20px#00abf0;

}
.content{
    text-align: center;
    margin: 15px;
    width: 60vw;
}
.content p{
   font-size: 1.2rem;
   text-align: justify;
}
.certificate{
    width: 40vw;
}
.certificate img{
    margin-left: 20px;
    width: 30vw;
    
}
@media screen and (max-width : 915px) {
    #About{
        padding:60px  0px;
        height: auto;
    }
   
    .Box{
        margin:15px;
        padding: 0px;
        flex-direction: column;
        width: 90vw;
    }
    .content{
        margin: 5px;
        margin-top: 20px;
        width: 80vw;
    }
    .content p{
        font-size: 1rem;
       
    }
    .certificate{
        width: 80vw;
    }
    .certificate img{
        margin: 15px 0;
        width: 80vw;
    }
}